import React from 'react';
import {
  Form, FormGroup, Label, FormText,
  Container, Row, Col,
  Collapse,
  InputGroup,
  InputGroupAddon,
  InputGroupButtonDropdown,
  InputGroupDropdown,
  Input,
  Button,
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
 } from 'reactstrap';

export default class SearchBar extends React.Component {
  constructor(props) {
    super(props);
    this.toggle = this.toggle.bind(this);
    this.state = {
      Collapse: false,
    };
  }
  toggle() {
    this.setState({ Collapse : !this.state.Collapse})
  }


  render() {
    return (
      <div className = "user-masthead">
        <InputGroup className = "user-search-bar">
          <Input placeholder="search..." />
          <InputGroupAddon addonType="append"><Button color="primary">Search</Button></InputGroupAddon>
          <InputGroupAddon addonType="append">
            <Button outline color="info" onClick={this.toggle}>
              Filter
            </Button></InputGroupAddon>
        </InputGroup>
        <Collapse isOpen={this.state.Collapse} className = "user-filter">

          <Row>

            <Col xs="4">
              <FormGroup className ="filter_College">
                <Label for="exampleSelect">學院</Label>
                <Input type="select" name="select" id="exampleSelect">
                  <option>理學院</option>
                  <option>商學院</option>
                  <option>3</option>
                  <option>4</option>
                  <option>5</option>
                </Input>
              </FormGroup>
            </Col>

            <Col xs="4">
              <FormGroup className ="filter_Department">
                <Label for="exampleSelect">學系</Label>
                <Input type="select" name="select" id="exampleSelect">
                  <option value = "CS">資訊科學系</option>
                  <option>心理學系</option>
                  <option>應用數學系</option>
                  <option>會計系</option>
                  <option>風險管理系</option>
                </Input>
              </FormGroup>
            </Col>

            <Col xs="4">
              <FormGroup className ="filter_Category">
                <Label for="exampleSelect">課程類別</Label>
                <Input type="select" name="select" id="exampleSelect">
                  <option>必修</option>
                  <option>選修</option>
                  <option>3</option>
                  <option>4</option>
                  <option>5</option>
                </Input>
              </FormGroup>
            </Col>

          </Row>
        </Collapse>
      </div>
    );
  }
}



